import { blunderPositions } from "./positions/BlunderPositions";

export function parseFEN(fen) {
  const pieceTypes = {
    p: "p",
    r: "r",
    n: "n",
    b: "b",
    q: "q",
    k: "k",
  };
  const rows = fen.split("/");
  const pieceDictionnary = {};

  for (let i = 0; i < rows.length; i++) {
    let j = 0;
    const row = rows[i];
    for (let k = 0; k < row.length; k++) {
      const char = row.charAt(k);
      if (/\d/.test(char)) {
        j += parseInt(char);
      } else {
        const color = char.toUpperCase() === char ? "w" : "b";
        const pieceType = pieceTypes[char.toLowerCase()];
        const id = getBoardPosition(i + 1, j + 1);
        pieceDictionnary[id] = { pieceType, color };
        j++;
      }
    }
  }
  return pieceDictionnary;
}

export function getBoardPosition(i, j) {
  const columns = ["a", "b", "c", "d", "e", "f", "g", "h"];
  const rows = ["8", "7", "6", "5", "4", "3", "2", "1"];
  return columns[j - 1] + rows[i - 1];
}

export function getBlunderPositions() {
  return blunderPositions;
}
