<template>
  <div class="options">
    <div
      v-for="c in choices"
      class="option"
      :key="'option-' + c"
      @click="emitChoice(c.id)"
    >
      <div class="content">
        <h4>{{ c.pict }}</h4>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PlayerChoices",
  components: {},
  data() {
    return {
      choices: [
        { pict: "jé", id: "jé" },
        { pict: "lg", id: "lg" },
        { pict: "mallo", id: "mallo" },
        { pict: "thom", id: "thom" },
        { pict: "ben", id: "ben" },
      ],
    };
  },
  methods: {
    emitChoice(choice) {
      this.$emit("choice", choice);
    },
  },
};
</script>
<style scoped>
.options {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 40px;
  width: 75%;
}
.option {
  width: 100%;
  padding-bottom: 100%;
  border: 2px solid var(--beige);
  position: relative;
  transition: all 250ms linear;
  cursor: pointer;
}
.option:hover {
  border: 2px solid var(--green);
}
.option .content {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.2);
}
</style>
