<template>
  <div class="container col">
    <h1 class="page-title" :class="mode === 'blunders' ? 'red' : 'blue'">
      {{ mode }}
    </h1>
    <div v-if="done" class="col done">
      <h1>Result:&nbsp;{{ totalPoints + "/" + totalPossiblePoints }}</h1>
      <h1>{{ pointsRatio }}</h1>
    </div>
    <div class="board-wrapper" v-else>
      <div class="board" :class="{ flipped: boardFlipped }">
        <div v-for="i in 8" class="board-row" :key="'row-' + i">
          <div v-for="j in 8" :key="'col-' + j">
            <div class="square" :class="getColor(i, j)">
              <div class="content">
                <ChessPiece
                  v-if="pieceDictionnary[getId(i, j)]"
                  :data="pieceDictionnary[getId(i, j)]"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col options">
        <div class="col">
          <div class="option row">
            <div class="btn" @click="showMove()">Move</div>
            <p v-if="onfirstMove && shown.move">Next</p>
            <p v-else-if="shown.move">Previous</p>
            <p class="red" v-else>-4pts</p>
          </div>
          <div class="option row">
            <div class="btn" @click="showEvalChange()">Eval Change</div>
            <p v-if="shown.evalChange">{{ currentPos.evaluationChange }}</p>
            <p class="red" v-else>-3pts</p>
          </div>
          <div class="option row">
            <div class="btn" @click="showTimeSpent()">Time Spent</div>
            <p v-if="shown.timeSpent">{{ currentPos.timeSpent }}</p>
            <p class="red" v-else>-2pts</p>
          </div>
          <div class="option row">
            <div class="btn" @click="showTimeFormat()">Time Control</div>
            <p v-if="shown.timeFormat">{{ currentPos.timeFormat }}</p>
            <p class="red" v-else>-1pts</p>
          </div>
        </div>
        <h1>{{ posPoints }}</h1>
      </div>
    </div>
    <Choices @choice="selectPlayer" v-if="!done"></Choices>
  </div>
</template>
<script>
import ChessPiece from "./ChessPiece.vue";
import Choices from "@/components/PlayerChoices.vue";
import {
  parseFEN,
  getBoardPosition,
  getBlunderPositions,
} from "@/scripts/ChessUtils.js";
export default {
  name: "ChessBoard",
  components: {
    ChessPiece,
    Choices,
  },
  props: ["mode"],
  data() {
    return {
      pieceDictionnary: {},
      columns: ["a", "b", "c", "d", "e", "f", "g", "h"],
      rows: ["8", "7", "6", "5", "4", "3", "2", "1"],
      positions: [],
      posIndex: 0,
      currentPos: {},
      fromPos: {},
      toPos: {},
      boardFlipped: false,
      onfirstMove: true,
      shown: {
        move: false,
        timeSpent: false,
        evalChange: false,
        timeFormat: false,
      },
      posPoints: 20,
      totalPoints: 0,
      done: false,
    };
  },
  mounted() {
    this.positions = this.shuffle(getBlunderPositions());
    this.loadNextPosition();
  },
  computed: {
    totalPossiblePoints() {
      return this.posPoints * this.positions.length;
    },
    pointsRatio() {
      return (
        Math.round((this.totalPoints * 100) / this.totalPossiblePoints) + "%"
      );
    },
  },
  methods: {
    selectPlayer(val) {
      if (val === this.currentPos.player) {
        this.totalPoints += this.posPoints;
      }
      if (this.posIndex < this.positions.length) {
        this.loadNextPosition();
      } else {
        this.done = true;
        this.posPoints = 20;
      }
    },
    getColor(i, j) {
      return (j + i) % 2 === 0 ? "w" : "b";
    },
    getId(i, j) {
      return getBoardPosition(i, j);
    },
    loadNextPosition() {
      this.posPoints = 20;
      this.shown = {
        move: false,
        timeSpent: false,
        evalChange: false,
        timeFormat: false,
      };
      this.onfirstMove = true;
      this.currentPos = this.positions[this.posIndex];
      this.posIndex++;
      this.fromPos = parseFEN(this.currentPos.move.from);
      this.toPos = parseFEN(this.currentPos.move.to);
      this.pieceDictionnary = this.fromPos;
      if (this.currentPos.color === "b") {
        this.boardFlipped = true;
      } else {
        this.boardFlipped = false;
      }
    },
    showMove() {
      if (!this.shown.move) {
        this.posPoints -= 4;
        this.shown.move = true;
      }
      if (this.onfirstMove) {
        this.pieceDictionnary = this.toPos;
      } else {
        this.pieceDictionnary = this.fromPos;
      }
      this.onfirstMove = !this.onfirstMove;
    },
    showTimeFormat() {
      if (!this.shown.timeFormat) {
        this.posPoints -= 1;
        this.shown.timeFormat = true;
      }
    },
    showTimeSpent() {
      if (!this.shown.timeSpent) {
        this.posPoints -= 2;
        this.shown.timeSpent = true;
      }
    },
    showEvalChange() {
      if (!this.shown.evalChange) {
        this.posPoints -= 3;
        this.shown.evalChange = true;
      }
    },
    shuffle(array) {
      let currentIndex = array.length,
        randomIndex;
      while (currentIndex != 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex],
          array[currentIndex],
        ];
      }
      return array;
    },
  },
};
</script>
<style scoped>
.done.col {
  align-items: center;
}
.board-wrapper {
  display: grid;
  grid-template-columns: 3fr 1.5fr;
  width: 100%;
}
.container {
  width: 100%;
  gap: 40px;
}
.board {
  width: 100%;
  background-color: aliceblue;
  margin: auto;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
}
.board.flipped {
  transform: rotate(180deg);
}
.board.flipped .square {
  transform: rotate(180deg);
}
.board .board-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  width: 100%;
}
.square {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
}
.square.b {
  background-color: var(--green);
}
.square.w {
  background-color: var(--beige);
}
.content {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.options {
  width: 100%;
  padding: 0 0 0 10px;
}

.option {
  justify-content: space-between;
  align-items: center;
}

.option p {
  font-size: 18px;
}

.piece-show {
  display: grid;
  grid-template-columns: 75px 75px 75px 75px 75px 75px;
  grid-template-rows: 75px;
  border: 1px solid var(--white);
}
h1 {
  margin: 0 auto;
}
h1.page-title {
  font-variant: small-caps;
  font-weight: 700;
}
.red {
  color: var(--red);
}
h1.blue {
  color: var(--l-blue);
}
</style>
